export default {
	data() {
		return {
			user_username:'',
			user_password:'',
			flag: false
		}
	},
	methods: {
		toRegister() {
			uni.navigateTo({
				url:'../register/register'
			})
		},
		getMessage() {
			let form={
				user_username: this.user_username,
				user_password: this.user_password
			}
			uni.request({
				url:'http://localhost:8088/user/login',
				data:form,
				method: "POST"
			}).then((res) => {
				let code = res[res.length-1].data.code
				if(code !== 0){
					this.flag = true
				}else{
					uni.redirectTo({
						url:'../index/index'
					})
					this.flag = false
				}
			}).catch((erro) => {
				console.log(erro)
			})
		}
	}
}
